<template>
    <div class="clearfix selector">
        <div class="type-wrap logo">
            <div class="fl key brand">品牌</div>
            <div class="value logos">
                <ul class="logo-list">
                    <li @click="$router.push({
                        path:'/search',
                        query:{
                            ...$route.query,
                            trademark:item.tmId+':'+item.tmName
                        }
                    })" v-for="item in trademarkList" :key="item.tmId">
                        {{item.tmName}}
                    </li>
                </ul>
            </div>
        </div>
        <div class="type-wrap" v-for="item in attrsList" :key="item.attrId">
            <div class="fl key">{{item.attrName}}</div>
            <div class="fl value">
                <ul class="type-list">
                    <li v-for="(info,index) in item.attrValueList" :key="index">
                        <a @click="addPropsGoSearch(item.attrId,info,item.attrName)">{{info}}</a>
                    </li>
                </ul>
            </div>
            <div class="fl ext"></div>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex";

export default {
    name: "SearchSelector",
    methods:{
        // attrId-->属性ID
        // info---->属性值
        // attrName--->属性名"
        addPropsGoSearch(attrId,info,attrName){
            // 添加的属性值
            const propsValue = attrId+':'+info+':'+attrName;
            // 判断props是否有值，如果无值，那么让其值为[]
            const props = this.$route.query.props || [];
            // 如果拥有该属性值，那么程序停止 。
            if(props.includes(propsValue)) return;
            this.$router.push({
                path:'/search',
                query:{
                    ...this.$route.query,
                    props:[...props,propsValue]
                }
            })
        }
    },
    computed:mapState("product",{
        trademarkList(state){
            return state.searchProductResult.trademarkList.filter(item=>item.tmName);
        },
        attrsList(state){
            return state.searchProductResult.attrsList;
        }
    })
}
</script>

<style lang="less" scoped>
.selector{
    border: 1px solid #ddd;
    margin-bottom: 5px;
    overflow: hidden;
    .logo{
        border-top: 0;
        margin: 0;
        position: relative;
        overflow: hidden;
        .key{
            padding-bottom: 87px!important;
        }
    }
    .type-wrap{
        margin: 0;
        position: relative;
        border-top: 1px solid #ddd;
        overflow:hidden;
        .key{
            width: 100px;
            background: #f1f1f1;
            line-height: 26px;
            text-align: right;
            padding: 10px 10px 0 15px;
            float:left;
        }
        .value{
            overflow: hidden;
            padding: 10px 0 0 15px;
            color: #333;
            margin-left: 120px;
            padding-right: 90px;
            .logo-list{
                li{
                    float: left;
                    border: 1px solid #e4e4e4;
                    margin: -1px -1px 0 0;
                    width: 105px;
                    height: 52px;
                    text-align: center;
                    line-height: 52px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-weight: 700;
                    color: #e1251b;
                    font-style: italic;
                    font-size: 14px;
                    img{
                        max-width: 100%;
                        vertical-align: middle;
                    }
                }
            }
            .type-list{
                li{
                    float: left;
                    display: block;
                    margin-right: 30px;
                    line-height: 26px;
                    a{
                        text-decoration: none;
                        color: #666;
                    }
                }
            }
        }
        .ext{
            position: absolute;
            top: 10px;
            right: 10px;
            .sui-btn{
                display: inline-block;
                padding: 2px 14px;
                box-sizing: border-box;
                margin-bottom: 0;
                font-size: 12px;
                line-height: 18px;
                text-align: center;
                vertical-align: middle;
                cursor: pointer;
                padding: 0 10px;
                background: #fff;
                border: 1px solid #d5d5d5;
            }
            a{
                color: #666;
            }
        }
    }
}
</style>